import { React } from 'react';
import Button from '@mui/material/Button';
import Input from '@mui/material/Input';
import CloudUploadIcon from '@mui/icons-material/CloudUpload';
import {Box, Grid, Alert, AlertTitle} from '@mui/material'


const FileUploadComponent = (props) => {
  return (
    <div style={{marginTop: '5rem'}}>
      <Grid container spacing={4} alignItems="center">
        <Grid item xs={12} md={5} sx={{order: {xs: 2, md: 1}, display: {xs: 'none', md: 'block'}}}>
          <img src='/static/wireframe.svg' alt='banner' />
        </Grid>
        <Grid item xs={12} md={7} sx={{order: {xs: 1, md: 2}}}>
          <Alert severity="info" icon={false} style={{marginBottom: '2rem'}}>
            <AlertTitle>Instructions</AlertTitle>
            <Box>
              <ol style={{textAlign: 'left'}}>
                <li>Please upload CT scan images of lung slices in DICOM or standard image formats (e.g., PNG, JPEG)</li>
                <li>Make sure the image represent slice of lung CT scans.</li>
                <li>After selecting, please wait for the image classification process to complete.</li>
                <li>Your uploaded images are processed locally on the server, and we do not store or share your medical data.</li>
                <li>
                  Disclaimer: This tool is designed for informational and demonstration purposes only. It is not a substitute for professional medical advice or diagnosis
                </li>
              </ol>
            </Box>
          </Alert>
          <Input
            type="file"
            onChange={event => props.handleFileChange(event)}
            style={{ display: 'none' }}
            id="fileInput"
          />
          <label htmlFor="fileInput">
            <Button
              variant="contained"
              component="span"
              size='large'
              startIcon={<CloudUploadIcon />}
            >
              Select and Analyse Image
            </Button>
          </label>
        </Grid>
      </Grid>
    </div>
  );
}

export default FileUploadComponent;
