import * as React from 'react';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import { Chip } from '@mui/material';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import * as data from '../../data/navbarData'
import Container from '@mui/material/Container';
import Button from '@mui/material/Button';
import AdbIcon from '@mui/icons-material/Adb';
import GitHubIcon from '@mui/icons-material/GitHub';


function ResponsiveAppBar({modelName}) {
  return (
    <AppBar position="static" color='transparent' elevation={1}>
      <Container maxWidth="xl">
        <Toolbar sx={{paddingY: 2}}>
          <Box sx={{display: 'flex', flexGrow: {xs: 1, md: 0}}} justifyContent="center" alignItems="center">
            <img alt='logo' src='/static/lungs.svg' style={{width: '70px'}} />
            <Typography
              variant="h5"
              noWrap
              component="a"
              href="/"
              sx={{
                ml: 2,
                mr: 2,
                fontFamily: 'monospace',
                fontWeight: 700,
                letterSpacing: '.3rem',
                color: '#384a66',
                textDecoration: 'none',
              }}
            >
              LungSpot
            </Typography>
          </Box>
          <Box sx={{marginLeft: '20px', display: {xs: 'none', md:'block'}}}>
            <Button href={data.sourceCodeUrl} target="_blank" color='primary' startIcon={<GitHubIcon />}>Source Code</Button>
          </Box>
          <Box sx={{flexGrow: 1, display: {xs: 'none', md:'block'}}}></Box>
          <Box sx={{ flexGrow: 0, marging: 'auto', display:{xs: 'none', md: 'block'} }}>
            <Chip  icon={<AdbIcon color="primary" />} label={`Model: ${modelName}`} />
          </Box>
        </Toolbar>
      </Container>
    </AppBar>
  );
}
export default ResponsiveAppBar;
