import React from 'react'
import {
  Paper, Grid, Box, Card, 
  CardContent, Typography, CardActions, 
  Button, ToggleButtonGroup, ToggleButton, Tooltip, IconButton 
} from '@mui/material'
import ArrowOutwardIcon from '@mui/icons-material/ArrowOutward';
import WarningAmberIcon from '@mui/icons-material/WarningAmber';
import ReplayIcon from '@mui/icons-material/Replay';
import NearbyErrorIcon from '@mui/icons-material/NearbyError';
import VerifiedUserIcon from '@mui/icons-material/VerifiedUser';
import AutoAwesomeIcon from '@mui/icons-material/AutoAwesome';
import DiamondIcon from '@mui/icons-material/Diamond';
import DiamondOutlinedIcon from '@mui/icons-material/DiamondOutlined';
import AutoAwesomeOutlinedIcon from '@mui/icons-material/AutoAwesomeOutlined';

const typeMapping = {
  malignant: {
    name: 'Malignant',
    color: '#ef233c',
    icon: WarningAmberIcon,
    messages: [
      'Malignant lung cancer can cause death,',
      'breathing problems, and pain',
      'It can spread to other organs and damage them'
    ],
    learnmoreUrl: 'https://en.wikipedia.org/wiki/Lung_cancer'
  },
  benign: {
    name: 'Benign',
    color: '#c6ac8f',
    icon: NearbyErrorIcon,
    messages: [
      'Benign lung cancer is a non-cancerous growth in the lung tissue or airways.',
      'Benign lung cancer usually does not require treatment,',
      'unless it causes significant symptoms or complications.'
    ],
    learnmoreUrl: 'https://cancer.ca/en/cancer-information/cancer-types/lung/what-is-lung-cancer/non-cancerous-tumours'
  },
  normal: {
    name: 'Healthy Lungs',
    color: '#4c956c',
    icon: VerifiedUserIcon,
    messages: [
      'Lungs seems healthy and functioning.',
      'Both healthy and cancerous lungs have similar structures, such as airways, air sacs, and blood vessels.'
    ],
    learnmoreUrl: 'https://lunghealth.lungfoundation.com.au/'
  },
}

const Result = ({result, resetResult, inputImgSrc, gradCamImgSrc}) => {
  const [mode, setMode] = React.useState('original');
  const handleImgMode= (event, newMode) => {
    if (newMode) {
      setMode(newMode);
    }
  };
  let type = typeMapping[result]
  const messages = type.messages.map(msg => (
    <Typography variant="body2">
      {msg}
    </Typography>
  ));
  return (
    <Box position="relative">
      <Grid container spacing={4} alignItems="center">
        <Grid item xs={12} md={5} >
          <Paper style={{ padding: '0px', textAlign: 'center', overflow: 'hidden' }}>
            <img alt='Result' src={mode === 'original' ? inputImgSrc : gradCamImgSrc} style={{width:'100%', height:"auto"}} />
          </Paper>
          <Box>
            <ToggleButtonGroup
              sx={{pt:2}}
              value={mode}
              exclusive
              onChange={handleImgMode}
              aria-label="ImageView Mode"
              size="small"
              color='primary'
            >
              <ToggleButton value="original" aria-label="left aligned">
                {mode === 'original' ? <DiamondIcon /> : <DiamondOutlinedIcon />}
              </ToggleButton>
              <ToggleButton value="gradcam" aria-label="centered">
                {mode === 'gradcam' ? <AutoAwesomeIcon /> : <AutoAwesomeOutlinedIcon />}
              </ToggleButton>
            </ToggleButtonGroup>
          </Box>
        </Grid>
        <Grid item xs={12} md={7} >
          <Card elevation={0} sx={{ minWidth: 275, borderRadius: 2 }}>
            <CardContent>
              <Typography  variant="h6" color="text.secondary" gutterBottom>
                Predicted Case
              </Typography>
              <Typography variant="h1" sx={{color: type.color, textShadow: ""}} component="div">
                {type.name}
              </Typography>
              <Box sx={{ mb: 1.5, mt: 2 }}>
                {messages}
              </Box>
            </CardContent>
            <CardActions sx={{textAlign: 'center', justifyContent: "center"}}>
              <Button size="small" variant='outlined' href={type.learnmoreUrl} endIcon={<ArrowOutwardIcon />}>Learn More</Button>
            </CardActions>
          </Card>
        </Grid>
      </Grid>
      <Box sx={{position: 'absolute', right: 0, top: 0}}>
        <Tooltip title="Run Again">
          <IconButton color='primary' onClick={()=>resetResult()}>
            <ReplayIcon sx={{fontSize: 30}} />
          </IconButton>
        </Tooltip>
      </Box>
      <type.icon sx={{position: 'absolute', color: type.color, right: 0, bottom: 0, opacity: 0.2, fontSize: 150}} />
    </Box>
  )
}

export default Result