import React, { Component } from 'react'
import FileUploadComponent from './Fileupload'
import Loader from './Loader'
import Result from './Result'
import { Box, Container } from '@mui/material'
import axios from 'axios'
import * as urls from '../../apiUrls';

class BodyComponent extends Component {
  constructor(props) {
    super(props);
    this.state = {
      result: null,
      loading: false,
      inputImgSrc: null,
      gradCamImgSrc: null,
      selectedFile: null
    }
  }
  getPrediction = async () => {
    if (this.state.selectedFile) {
      let formdata = new FormData();
      formdata.append('file', this.state.selectedFile);
      let response = await axios.post(urls.predictCaseUrl, formdata, {
        headers: {
          "Content-Type": 'multipart/formdata'
        }
      })
      let data = await response.data;
      let inputImgResponse = await fetch(urls.baseUrl + data.input_image_url);
      let gradCamImgResponse = await fetch(urls.baseUrl + data.gradcam_image_url);
      let inputImgBlob = await inputImgResponse.blob();
      let gradCamImgBlob = await gradCamImgResponse.blob();
      this.setState({
        ...this.state,
        result: data.case,
        inputImgSrc: URL.createObjectURL(inputImgBlob),
        gradCamImgSrc: URL.createObjectURL(gradCamImgBlob),
        loading: false,
      })

    }
  }
  handleFileChange = (event) => {
    const file = event.target.files[0];
    this.setState({
      ...this.state,
      selectedFile: file
    }, () => {
      this.getPrediction();
      this.setLoading(true);
    })
  };
  setLoading = status => {
    this.setState({
      ...this.state,
      loading: status
    })
  }
  setImgSrc = src => {
    this.setState({
      ...this.state,
      imgSrc: src
    })
  }
  resetResult = () => {
    this.setState({
      ...this.state,
      result: null
    })
  }

  render() {
    if (this.state.loading) {
      return <Loader />;
    }
    return (
      <Container>
        {
          this.state.result ? 
          (<Result result={this.state.result} inputImgSrc={this.state.inputImgSrc} gradCamImgSrc={this.state.gradCamImgSrc} resetResult={this.resetResult} />)
          : <FileUploadComponent handleFileChange={this.handleFileChange} />
        }
      </Container>
    )
  }
}



export default BodyComponent