import React, { Component } from 'react'
import HeaderComponent from './header/HeaderComponent'
import BodyComponent from './body/BodyComponent'
import SplashComponent from './body/SplashComponent'
import axios from 'axios'
import { Fade } from '@mui/material'
import * as urls from '../apiUrls'

class MainComponent extends Component {
  constructor(props) {
    super(props);
    this.state = {
      serverConnected: false,
      modelName: null
    }
  }

  async connectServer() {
    console.log("did m");
    try {
      let response = await axios.get(urls.getModelNameUrl);
      let data = response.data;
      this.setState({
        ...this.state,
        serverConnected: true,
      });
      setTimeout(()=>{
        this.setState({
          ...this.state,
          modelName: data.modelname
        })
      }, 2000)
    } catch (error) {
      console.log(error);
    }
  }

  componentDidMount() {
    this.connectServer();
  }

  render() {
    return (
      <div>
          {
            this.state.modelName !== null ?
            <Fade in={true}>
              <div>
                <HeaderComponent modelName={this.state.modelName} />
                <BodyComponent />
              </div>
            </Fade>
            : <SplashComponent serverConnected={this.state.serverConnected} />
          }
      </div>
    )
  }
  
}

export default MainComponent