import React from 'react'
import { Typography, Box, CircularProgress, Fade } from '@mui/material'

const SplashComponent = ({serverConnected}) => {
  return (
    <Box className="splashscreen">
        <Box>
            <img src='/static/lungs.svg' width="150px" />
        </Box>
        <Fade in={true}>
            <Typography 
                variant='h2'
                sx={{
                    fontFamily: 'monospace',
                    fontWeight: 700,
                    letterSpacing: '.5rem',
                    color: '#384a66'
                }}
            >LungSpot
            </Typography>
        </Fade>
        <Box sx={{mt: 5}}>
            {serverConnected ? null : <CircularProgress />}
        </Box>
    </Box>
  )
}

export default SplashComponent